<script>
import axios from "axios";

export default {
  data() {
    return {
      newsPage: 1,
      page: 1,
      perPage: 3,
      contentsData: [],
      newsContent: [],
      items: [
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me 2" },
      ],
      argiItems: [],
      activeBtn: 1,
      colors: [
        "indigo",
        "warning",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
      ],
      slides: ["First", "Second", "Third", "Fourth", "Fifth"],
    };
  },
  methods: {
    getContentFirstPage() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContentFirstPage.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            console.log(response.data);
            this.contentsData = response.data.content;
            response.data.content.map((content, index) => {
              this.contentsData[
                index
              ].contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${content.contentImg}`;
            });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getContent() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContent.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            this.newsContent = response.data.content.filter(
              (e) => e.contentTypeTitle === "ข่าวประชาสัมพันธ์"
            );
          }
          this.newsContent.map((e, i) => {
            e.contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${e.contentImg}`;
          });
          console.log("newsContent", this.newsContent);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getTypeOfArgi() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/getAllTypeOfArgiTD.php"
        )
        .then((response) => {
          if (response.data.typeOfArgi.length > 0) {
            this.argiItems = response.data.typeOfArgi;
            this.argiItems = this.argiItems.filter((e, i) => {
              return e.nameTypeOfArgi_MK !== null;
            });
            console.log("getAllTypeOfArgiTD", this.argiItems);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    visiblePages() {
      return this.contentsData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
    visibleNewsPages() {
      return this.newsContent.slice(
        (this.newsPage - 1) * this.perPage,
        this.newsPage * this.perPage
      );
    },
  },
  mounted() {
    this.getContent();
    this.getContentFirstPage();
    this.getTypeOfArgi();
  },
};
</script>
